import React from "react";
import Shelf from "./Shelf/Shelf";
import Content from "./Content/Content";

const shelfList = props => {
  const linkName = ['omnifood', 'natours', 'forkify', "pig-game", 'budgety']

  return (
    <ul className="ShelfList">
      {props.projects.map((project, index) => {
        return (
          <div key={project.id}>
            <Shelf className="Shelf" name={project.name} showContent={props.showContent} id={project.id} />
            {project.doesShow ? <Content linkName={linkName[index]} name={project.name} text={project.text} /> : null}
          </div>
        );
      })}
    </ul>
  );
};

export default shelfList;
